import React, {Component} from 'react';
import axios from 'axios';
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import {DoctorPatientsListComponent} from './DoctorPatientsListComponent';

export class DoctorPatientsContainer extends Component {

    constructor(props) {
        super(props);
        this.state = {patients: []};
    }

    componentDidMount = () => {
        axios.get('http://localhost:8081/api/doctors' + this.props.match.params.id + '/patients')
            .then((response) => {
                this.setState({patients: response.data});
            })
            .catch((error) => {
                console.log(error);
                console.log(this.props.match.params.id);
            });
    };

    render() {
        return (
            <div>
                <DoctorPatientsListComponent patients={this.state.patients} history={this.props.history} />
            </div>
        );
    }
}



