import React from 'react';
import axios from 'axios';

const styles = {
  color: 'red'
}

export var DoctorPatientsComponent = (props) => {
  var {id, name, surname, username, personalId, dateOfBirth} = props;

  return (
      <tr>
        <td>{id}</td>
        <td>{name}</td>
        <td>{surname}</td>
        <td>{username}</td>
        <td>{personalId}</td>
        <td>{dateOfBirth}</td>
      </tr>
  );

};



